<template>
  <el-menu
          :default-active="$route.path"
          class="el-menu-vertical-demo aSider"
          background-color="transparent"
          text-color="#fff"
          active-text-color="#ffd04b"
          router
  >
    <el-submenu v-for="(item,index) in navPath" v-if="tree.indexOf(item.power)>-1" :index="item.index" :key="index" style="text-align: left;">
      <template slot="title">
        <span><svg-icon :icon-class="item.icon" class="asiderClass"></svg-icon></span>
        <span style="font-size: 20px;vertical-align: top;">{{item.name}}</span>
      </template>
      <el-menu-item-group>
        <!--<template slot="title"></template>-->
        <el-menu-item v-for="(temp, index) in item.nextNav" :key="index" v-if="tree.indexOf(temp.power)>-1" :index="temp.index">
          <!--<i :class="temp.icon"></i>-->
          <svg-icon :icon-class="temp.icon" class="twoAsiderClass"></svg-icon>
          <span style="font-size: 16px;">{{temp.name}}</span>
          <el-badge style="vertical-align: top;margin-left: 10px;" v-show="temp.name === '' && bubbleNum !== '0' " class="mark" :value="bubbleNum" />
        </el-menu-item>
      </el-menu-item-group>
    </el-submenu>
  </el-menu>
</template>
<script>
import store from '@/store/index'
export default {
  name: 'aSider',
  store,
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      activeNav: '/reference/theme',
      tree: [],
      navPath: [
        {
          name: ' ',
          index: '1',
          power: 'a1',
          icon: 'reference',
          nextNav: [
            {
              name: '',
              index: '/reference/theme',
              power: 'a1_b1',
              icon: 'referenceTheme'
            },
            {
              name: '',
              index: '/reference/matter',
              power: 'a1_b2',
              icon: 'referenceMatter'
            },
            {
              name: '',
              index: '/reference/element',
              power: 'a1_b3',
              icon: 'referenceElement'
            }
          ]
        },
        {
          name: ' ',
          index: '2',
          power: 'a2',
          icon: 'call',
          nextNav: [
            {
              name: '',
              index: '/call',
              power: 'a2_b1',
              icon: 'callNum'
            }
          ]
        },
        {
          name: ' ',
          index: '3',
          power: 'a3',
          icon: 'task',
          nextNav: [
            {
              name: '',
              index: '/deal',
              power: 'a3_b1',
              icon: 'deal'
            }
          ]
        },
        {
          name: ' ',
          index: '4',
          power: 'a4',
          icon: 'data',
          nextNav: [
            {
              name: '',
              index: '/dataAnalysis',
              power: 'a4_b1',
              icon: 'dataAnalysis'
            }
          ]
        },
        {
          name: '',
          index: '5',
          power: 'a5',
          icon: 'elementLibrary',
          nextNav: [
            {
              name: '',
              index: '/Administration/ATheme',
              power: 'a5_b1',
              icon: 'ATheme'
            },
            {
              name: '',
              index: '/Administration/AMatter',
              power: 'a5_b2',
              icon: 'AMatter'
            },
            {
              name: '',
              index: '/Administration/AElement',
              power: 'a5_b3',
              icon: 'AElement'
            }
          ]
        },
        {
          name: '',
          index: '6',
          power: 'a6',
          icon: 'hall',
          nextNav: [
            {
              name: '',
              index: '/Ahall/AAddress',
              power: 'a6_b1',
              icon: 'Address'
            },
            {
              name: '',
              index: '/Ahall/AWindow',
              power: 'a6_b2',
              icon: 'Window'
            },
            {
              name: '',
              index: '/Ahall/AMessage',
              power: 'a6_b3',
              icon: 'Message'
            }
          ]
        },
        {
          name: '',
          index: '7',
          power: 'a7',
          icon: 'Account',
          nextNav: [
            {
              name: '',
              index: '/Acconut/AUser',
              power: 'a7_b1',
              icon: 'AUser'
            },
            {
              name: '',
              index: '/Acconut/ARole',
              power: 'a7_b2',
              icon: 'ARole'
            },
            {
              name: '',
              index: '/Acconut/AJurisdiction',
              power: 'a7_b3',
              icon: 'AJurisdiction'
            }
          ]
        }
      ]
    }
  },
  computed: {
    bubbleNum: function () {
      return this.$store.state.bubbleNum
    }
  },
  created: function () {
    if (sessionStorage.userInfo) {
      this.tree = JSON.parse(sessionStorage.userInfo).allPermission
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .asiderClass{
    width: 20px!important;
    height: 20px!important;
  }
  .twoAsiderClass{
    width: 16px!important;
    height: 16px!important;
  }
</style>
<style lang="scss">
  .aSider{
    .el-submenu__title i{
      color: #ffffff;
    }
  }
</style>



