import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    count: 0,
    isAside: 0,
    bubbleNum: sessionStorage.bubbleNum ? sessionStorage.bubbleNum : 0,
    detailsMatter: ''
  },
  mutations: {
    increase () {
      this.state.count++
      console.log('this.state.count', this.state.count)
    },
    changeBackAside () {
      this.state.isAside++
      console.log('this.state.isAside', this.state.isAside)
    },
    changeBubbleNum () {
      this.state.bubbleNum = sessionStorage.bubbleNum ? sessionStorage.bubbleNum : 0
      console.log('this.state.bubbleNum', this.state.bubbleNum)
    },
    changeDetailsMatter () {
      this.state.detailsMatter = sessionStorage.detailsMatter ? sessionStorage.detailsMatter : ''
    }
  },
  actions: {

  }
})



