<template>
  <div id="app">
    <el-container class="container">
      <el-aside width="200px" class="webAside">
        <aSider v-if="isAside"></aSider>
      </el-aside>
      <el-container>
        <el-header class="webHeader">
          
          <div class="signOutWarp">
            <el-button type="text" class="signOutBtn" @click="outBtnFn">
              <svg-icon icon-class="signOut" class="outIcon"></svg-icon>
            </el-button>
          </div>
        </el-header>
        <el-main class="webMain">
          <keep-alive>
            <router-view v-if="$route.meta.keepAlive"/>
          </keep-alive>
          <router-view v-if="!$route.meta.keepAlive"/>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import lodash from 'lodash'
import aSider from '@/components/aSider'
import store from '@/store/index'
let bubbleNumTimer = 1
export default {
  name: 'App',
  store,
  data () {
    return {
      isAside: true
    }
  },
  components: { aSider },
  watch: {
    watchAside (val) {
      this.isAside = false
      this.$nextTick(() => {
        this.isAside = true
      })
    }
  },
  computed: {
    watchAside: function () {
      return this.$store.state.isAside || 0
    }
  },
  created: function () {
    // 
    clearInterval(bubbleNumTimer)
    let self = this
    if (sessionStorage.user_id) {
      self.getBubbleNumFn({user_id: sessionStorage.user_id})
    }
    bubbleNumTimer = setInterval(function () {
      if (sessionStorage.user_id) {
        self.getBubbleNumFn({user_id: sessionStorage.user_id})
      }
    }, 1000 * 60 * 3)
    //
    store.commit('changeBackAside')
  },
  methods: {
    outBtnFn: lodash.debounce(function () {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        if (sessionStorage.user_id) {
          this.signOutFn({
            user_id: sessionStorage.user_id
          })
        } else {
          this.notifyFn('', '')
          this.$router.push('/mobileSign')
        }
      }).catch(() => {
      })
    }, 600),
    async signOutFn (params) {
      try {
        let res = await this.$api.unsplash.signOut(params)
        console.log(res)
        if (res.errno === '0') {
          sessionStorage.clear()
          this.$router.push('/')
        } else {
          this.notifyFn('', '')
        }
      } catch (e) {
        this.notifyFn('', '')
        console.log(e)
      }
    },
    async getBubbleNumFn (params) {
      try {
        let res = await this.$api.unsplash.getBubbleNum(params)
        console.log(12, res)
        if (res.errno === '0') {
          sessionStorage.bubbleNum = res.data.total
          store.commit('changeBubbleNum')
        } else {
          this.notifyFn('', res.errmsg)
          sessionStorage.bubbleNum = 0
          store.commit('changeBubbleNum')
        }
      } catch (e) {
        this.notifyFn('', '')
        console.log(e)
        sessionStorage.bubbleNum = 0
        store.commit('changeBubbleNum')
      }
    },
    notifyFn (title, msg, type = 'warning') {
      this.$notify({
        title: title,
        message: msg,
        type: type,
        dangerouslyUseHTMLString: true,
        duration: 5000,
        position: 'top-right'
      })
    },
    myAlert (txt, title) {
      this.$alert(txt, title, {
        confirmButtonText: '',
        callback: action => {
        }
      })
    }
  }
}
</script>

<style lang="scss">
  *{
    margin: 0;
    box-sizing:border-box;
  }
  $hedeaerH: 80px;
  $BH: 40px;
  $asiderW: 300px;
  $contentPadding: 10px;

  html,body{
    min-width: 100vw;
    min-height: 100vh;
    #app {
      font-family: 'Avenir', Helvetica, Arial, sans-serif;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      text-align: center;
      color: #2c3e50;
      min-width: 100vw;
      min-height: 100vh;
      margin-top: 0;
      position: relative;
      .container{
        width: 100vw;
        height: 100vh;
        .webAside{
          /*<!--border-top: $hedeaerH solid transparent;-->*/
          padding-top: $hedeaerH;
          /*background: rgb(84, 92, 100);*/
          background-color: #3478cd;
          background-image: linear-gradient(90deg, #3478cd 0%, #3478cd 74%);
          overflow-y: scroll;
          &::-webkit-scrollbar {display:none}
        }
        .webHeader{
          min-height: $hedeaerH;
          font-size: 40px;
          line-height: $hedeaerH;
          text-align: center;
          color: #ffffff;
          letter-spacing: 4px;
          background: #3478cd;
          position: relative;
          .signOutWarp{
            width: $hedeaerH;
            height: $hedeaerH;
            position: absolute;
            top: 0;right: 0;
            .signOutBtn{
              width: $hedeaerH/2.5;
              height: $hedeaerH/2.5;
              padding: 0;
              position: absolute;
              top: 50%;left: 50%;
              transform: translate(-50%, -50%);
              &:hover{
                color: #ff361e;
              }
              .outIcon{
                width: $hedeaerH/2.5;
                height: $hedeaerH/2.5;
              }
            }
          }
        }
        .webMain{
          overflow-y: scroll;
          &::-webkit-scrollbar {display:none}
        }
      }
    }
  }
</style>



