import Vue from 'vue'
import Router from 'vue-router'
// import axios from 'axios'

Vue.use(Router)
export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Sign',
      component: () => import('@/components/sign/sign'),
      meta: {
        name: '',
        keepAlive: false
      }
    },
    {
      path: '/mobileSign',
      name: 'mobileSign',
      component: () => import('@/components/sign/mobileSign'),
      meta: {
        name: '',
        keepAlive: false
      }
    },
    {
      path: '/fix',
      name: 'fix',
      component: () => import('@/components/sign/fix'),
      meta: {
        name: '',
        keepAlive: false
      }
    },
    {
      path: '/reference/theme',
      name: 'ServiceTheme',
      component: () => import('@/components/reference/ServiceTheme'),
      meta: {
        name: '',
        keepAlive: false
      }
    },
    {
      path: '/reference/matter',
      name: 'ServiceMatter',
      component: () => import('@/components/reference/ServiceMatter'),
      meta: {
        name: '',
        keepAlive: false
      }
    },
    {
      path: '/reference/element',
      name: 'ServiceElement',
      component: () => import('@/components/reference/ServiceElement'),
      meta: {
        name: '',
        // keepAlive: true
        keepAlive: false
      }
    },
    {
      path: '/call',
      name: 'Call',
      component: () => import('@/components/call/call'),
      meta: {
        name: '',
        keepAlive: true
      }
    },
    {
      path: '/mobileCall',
      name: 'mobileCall',
      component: () => import('@/components/call/mobileCall'),
      meta: {
        name: '',
        keepAlive: true
      }
    },
    {
      path: '/deal',
      name: 'Deal',
      component: () => import('@/components/deal/deal'),
      meta: {
        name: '',
        keepAlive: false
      }
    },
    {
      path: '/dataAnalysis',
      name: 'dataAnalysis',
      component: () => import('@/components/data/dataAnalysis'),
      meta: {
        name: '',
        keepAlive: false
      }
    },
    {
      path: '/dataAnalysis/matterDetails',
      name: 'matterDetails',
      component: () => import('@/components/data/matterDetails'),
      meta: {
        name: '',
        keepAlive: false
      }
    },
    {
      path: '/tabData',
      name: 'tabData',
      component: () => import('@/components/data/tabData'),
      meta: {
        name: '',
        keepAlive: false
      }
    },
    {
      path: '/Administration/ATheme',
      name: 'ATheme',
      component: () => import('@/components/Administration/theme'),
      meta: {
        name: '',
        keepAlive: true
      }
    },
    {
      path: '/Administration/AMatter',
      name: 'AMatter',
      component: () => import('@/components/Administration/matter'),
      meta: {
        name: '',
        keepAlive: true
      }
    },
    {
      path: '/Administration/AElement',
      name: 'AElement',
      component: () => import('@/components/Administration/element'),
      meta: {
        name: '',
        keepAlive: true
      }
    },

    {
      path: '/Ahall/AAddress',
      name: 'AAddress',
      component: () => import('@/components/hall/address'),
      meta: {
        name: '',
        keepAlive: true
      }
    },
    {
      path: '/Ahall/AWindow',
      name: 'AWindow',
      component: () => import('@/components/hall/window'),
      meta: {
        name: '',
        keepAlive: true
      }
    },
    {
      path: '/Ahall/AMessage',
      name: 'AMessage',
      component: () => import('@/components/hall/shortMsg'),
      meta: {
        name: '',
        keepAlive: true
      }
    },

    {
      path: '/Acconut/AJurisdiction',
      name: 'AJurisdiction',
      component: () => import('@/components/account/jurisdiction'),
      meta: {
        name: '',
        keepAlive: true
      }
    },
    {
      path: '/Acconut/ARole',
      name: 'ARole',
      component: () => import('@/components/account/role'),
      meta: {
        name: '',
        keepAlive: true
      }
    },
    {
      path: '/Acconut/AUser',
      name: 'AUser',
      component: () => import('@/components/account/user'),
      meta: {
        name: '',
        keepAlive: true
      }
    },
    {
      path: '*',
      component: () => import('@/components/errPage/404'),
      meta: {title: ''}
    }
  ]
})



